import React, { Component } from 'react';
import GradeResult from './GradeResult'

class Grade extends Component {
    constructor(props){
        super(props)
        this.state = {
            grade: ""
        }
    }
    convertToGrade = (e) => {
       this.setState({
           grade: this.convert(e.target.score.value)
       })
       e.preventDefault()
    }
    convert(score) {
        if(score>=80){
            return "A"
        }else if(score>=70){
            return "B"
        }
        else if(score>=60){
            return "C"
        }else return "N/A"
    }
 
 render() {
    return (
        <div>
            <p>Grade convertor</p>
            <form onSubmit={this.convertToGrade} method="GET">
                <input type="text" id="score" name="score"/>
            </form>
            <GradeResult result={this.state.grade}/>
        </div>
    )
  }
}

export default Grade;



