import React, { Component } from 'react';

class Welcome extends Component {
    constructor(props){
        super(props)
        this.state = {
            name: 'N/A'
        }
        this.change = this.change.bind(this)
    }
    call(){
        setInterval(
            () => this.tick(),
            1000
        )
    }
    tick() {
        this.setState({
            time: new Date().toLocaleTimeString()
        }
        )
    }
    // change = (e) =>{
    //     this.setState({
    //         name: e.target.value
    //     })
    // }
    change(e){
        this.setState({
            name: e.target.value
        }
        )
    }
 render() {
     this.call()
     console.log("Render")
    return (
        <div>
            <input type="text" 
                value={this.state.name} 
                onChange={this.change}
                />
                <br/>
            {this.state.name}
        </div>
    )
  }
}

export default Welcome;



